import { Formik } from "formik";
import React from "react";
import { Form } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { getOptionsFilters } from "../../helpers";
import { color } from "../../helpers/const";
import Input from "../Input/Input";

const Search = ({
    setMessageInForm = ()=>({
        textMessage: '',
        colorMessage: ''
    }),
    setIsLoading = () =>{},
    setResaultSearch,
    oldMessage,
}) => {
    const { dispatch } = useStoreon();  

    const handlerSubmitSearch = data => {
        console.log({data})
        const params = {
            ...data,
            page: 1,
            page_size: 30,
            setIsLoading: setIsLoading,
            url: '/api_search_filters/',
            dataRequst: res => { console.log({search: res})
                if (res?.result?.status) {
                    setMessageInForm({
                        colorMessage: '#ff0000',
                        textMessage: res?.result?.status
                    })
                    return setResaultSearch(c => ({ ...c, count: 0, results: [] }));
                }
                if (!!res?.error) return
                setMessageInForm({
                    textMessage: 'УПС..., возникла не предвиденная ошибка, сообщите о ней Вашему менеджеру',
                    colorMessage: '#ff0000',
                })
                setMessageInForm({
                    colorMessage: color.bgc__green,
                    textMessage: `найдено ${res?.result?.results?.length ?? 0} совпадений`
                });
                if (res?.result?.count) {
                    const copyDataBrands = res?.result?.brands.slice() ?? [];
                    const copyDatamodels = res?.result?.models.slice() ?? [];
                    const copyDataGenerations = res?.result?.generations.slice() ?? [];
                    setResaultSearch(c => ({
                        ...c,
                        ...res.result,
                        brands: [...getOptionsFilters(copyDataBrands)],
                        models:[...getOptionsFilters(copyDatamodels)],
                        generations:[...getOptionsFilters(copyDataGenerations)],

                    }))
                    setMessageInForm({
                        colorMessage: color.bgc__green,
                        textMessage: `найдено ${res?.result?.count ?? 0} совпадений`
                    });
                }else{
                    return setResaultSearch(c => ({ ...c, count: 0, results: [] }));
                }
            },
        };
        dispatch('getContextSearch', params);
    }


    return (
        <React.Fragment>
            <Formik
                initialValues={{
                    q: '',
                    page: 1,
                    page_size: 30
                }}
                onSubmit={handlerSubmitSearch}
            >
                {
                    ({ values, handleChange }) => {
                        return (
                            <React.Fragment>
                                <Form
                                    onChange={handleChange}
                                    style={{
                                        width: '100%',
                                        padding: '0 15px',
                                        position: 'relative',
                                    }}
                                >
                                    <Input
                                        value={values.username}
                                        placeholder={'поиск'}
                                        name={'q'}
                                        id={1}
                                        onChange={e => {
                                            const value = e.target.value;
                                            if (value.length > 1) {
                                                handlerSubmitSearch({
                                                    q: value,
                                                })
                                            }else{
                                                setResaultSearch(c => ({
                                                    ...c,
                                                    count: 0,
                                                    brands: [],
                                                    models:[],
                                                    generations:[],
                            
                                                }));
                                                setMessageInForm({...oldMessage});
                                            }
                                        }
                                        }
                                    />
                                </Form>
                                {/* <Offset mb={90} /> */}
                            </React.Fragment>
                        )
                    }
                }
            </Formik>
        </React.Fragment>
    )
}

export default Search;