import React from "react";
import { Link } from "react-router-dom";
import { down } from "../../images";
import CheckBox from "../CheckBox/CheckBox";
import Icon from "../Icon/Icon";
import Spinner from "../Spinner";

import style from './styles/cartfilter.module.scss';

const CardFilters = ({
    list,   
    handlerChangeData,
    name_button,
    localPath,
    old_state,
    selectProduct,
    type_list,
    brand_id,
    model_id,
    generation_id,
    type,
    itemCountry,
}) =>{

    return (
        <React.Fragment>
            <ul
                className={style['card-filter__container']}
            >
                {
                    list.length?
                        list.map( (el, i )=> {
                            return (
                                <li
                                    key={`CardFilters-${i}`}
                                    className={style['card-filter__item']}
                                >
                                    <CheckBox
                                        checked={selectProduct.includes(el.value === ''? 0 : el.value )}
                                        onChange={ res => handlerChangeData(res)}
                                        name={old_state?.dataFilters?.page}
                                        value={el.value}
                                        id={`check-${el.value}`}
                                    />

                                    {
                                        !model_id?
                                            <Link
                                                to={`${localPath}${el.value}`}
                                                state = {{
                                                    old_name_button: old_state?.old_name_button,
                                                    old_state: old_state,
                                                    dataFilters: old_state,
                                                    name_button: name_button,
                                                    brand_id: brand_id ?? type_list === 'brands'? el.value : '',
                                                    model_id: model_id ?? type_list === 'models'? el.value : '',
                                                    generation_id: generation_id ?? type_list === 'generations'? el.value : '',
                                                    type: type,
                                                    cities: list?.map( item => item.value === el.value? item.cities : null ).filter(el => el !== null) ?? [],
                                                    itemCountry: itemCountry ?? null,
                                                }}
                                                className={style['card-filter__wrap-title']}
                                                style={{
                                                    pointerEvents: selectProduct.includes(el.value === ''? 0 : el.value)? 'none' : el.value === ''? 'none' : 'all',
                                                    color: selectProduct.includes(el.value)? '#c3c3c3' : '#000'
                                                }}
                                            >
                                                <Icon 
                                                    image={el.image} 
                                                    className={style['card-filter__icon-list']}    
                                                />
                                                <span
                                                    className={style['card-filter__name']}
                                                    >
                                                    {el.title}
                                                </span>
                                                {
                                                    el.childEl?
                                                        <Icon
                                                            image={down} 
                                                            className={style['card-filter__icon-child']}    
                                                        />
                                                        : null
                                                }
                                            </Link>
                                            : <React.Fragment>
                                                <Icon 
                                                    image={el.image} 
                                                    className={style['card-filter__icon-list']}    
                                                />
                                                <span
                                                    className={style['card-filter__name']}
                                                    >
                                                    {el.title}
                                                </span>
                                            </React.Fragment>
                                    }
                                    
                                </li>
                            )
                        })
                        : <Spinner />
                }
            </ul>
        </React.Fragment>
    )
}

export default CardFilters;