import classNames from "classnames";
import React from "react";
import { getOptionsPaymentPeriod } from "../../helpers";
import Icon from "../Icon/Icon";


import style from './styles/formpaymentaccess.module.scss';

const FormPaymentAccess = ({
    setDataButtonBottonTrigger,
    messageInFormDop,
    setFieldValue,
    typeAccess,
    values,
    state,
    tg,
}) => {

    return (
        <React.Fragment>
            <div
                className={style['form-payment-access__container']}
            >
                <div
                    className={style['form-payment-access__nav-container']}
                >
                    <div
                        className={classNames({
                            [style['form-payment-access__nav-button']]: true,
                            [style['form-payment-access__nav-button--active']]: values.access_text === typeAccess[0].title
                        })
                        }
                        onClick={e => {
                            setFieldValue('access_text', typeAccess[0].title);
                            setFieldValue('access', typeAccess[0].key_value);
                            setFieldValue('bank_text', '');
                            setFieldValue('bank', '');
                            setFieldValue('period_text', '');
                            setFieldValue('period', '');
                            setFieldValue('optionsPeriod', getOptionsPaymentPeriod(state.drop_down_list_period,typeAccess[0].key_value));
                            setDataButtonBottonTrigger(false,tg)
                        }}
                    >
                        {typeAccess[0].title}
                    </div>
                    <div
                        className={classNames({
                            [style['form-payment-access__nav-button']]: true,
                            [style['form-payment-access__nav-button--active']]: values.access_text === typeAccess[1].title
                        })
                        }
                        onClick={e => {
                            setFieldValue('access_text', typeAccess[1].title);
                            setFieldValue('access', typeAccess[1].key_value);
                            setFieldValue('bank_text', '');
                            setFieldValue('bank', '');
                            setFieldValue('period_text', '');
                            setFieldValue('period', '');
                            setFieldValue('optionsPeriod', getOptionsPaymentPeriod(state.drop_down_list_period,typeAccess[1].key_value));
                            setDataButtonBottonTrigger(false,tg)
                        }}
                    >
                        {typeAccess[1].title}
                    </div>
                </div>
                <div
                    className={style['form-payment-access__discription']}
                >
                    {
                        values.access === 'purchase' ?
                            <div
                                className={style['form-payment-access__discription-wrap']}
                            >

                                <div
                                    className={style['form-payment-access__discription-title']}
                                >
                                    {messageInFormDop?.textMessage?.limited_access?.info.title}
                                </div>
                                <ul
                                    className={style['form-payment-access__discription-context-list']}
                                >
                                    {
                                        messageInFormDop?.textMessage?.limited_access?.info.description.map((el, i) => {

                                            return (
                                                <li
                                                    key={i}
                                                    className={style['form-payment-access__discription-context-item']}
                                                >
                                                    <Icon
                                                        image={el.image}
                                                    />
                                                    <div
                                                        className={style['form-payment-access__discription-context-item-title']}
                                                    >

                                                        {el.title_desc}
                                                    </div>
                                                    <div
                                                        className={style['form-payment-access__discription-context-item-desc']}
                                                    >
                                                        {el.desc}
                                                    </div>
                                                </li>
                                            )
                                        })
                                    }

                                </ul>

                            </div>
                            :
                            <div
                                className={style['form-payment-access__discription-wrap']}
                            >
                                <div
                                    className={style['form-payment-access__discription-title']}
                                >
                                    {messageInFormDop?.textMessage?.full_access?.info.title}
                                </div>
                                <ul
                                    className={style['form-payment-access__discription-context-list']}
                                >
                                    {
                                        messageInFormDop?.textMessage?.full_access?.info.description.map((el, i) => {

                                            return (
                                                <li
                                                    key={i}
                                                    className={style['form-payment-access__discription-context-item']}
                                                >
                                                    <Icon
                                                        image={el.image}
                                                    />
                                                    <div
                                                        className={style['form-payment-access__discription-context-item-title']}
                                                    >

                                                        {el.title_desc}
                                                    </div>
                                                    <div
                                                        className={style['form-payment-access__discription-context-item-desc']}
                                                    >
                                                        {el.desc}
                                                    </div>
                                                </li>
                                            )
                                        })
                                    }

                                </ul>

                            </div>
                    }
                </div>

            </div>
        </React.Fragment>
    )
}

export default FormPaymentAccess;