import { color } from "./const";

export const setDataButtonBottonTrigger = (
  status = true,
  tg,
) => {
  if (status) {
    return tg.MainButton.setParams({
      color: tg.themeParams.button_color,
      is_active: true,
      is_visible: true,
    });
  }
  if (!tg.MainButton?.isActive) return;
  return tg.MainButton.setParams({
    color: color.bgc__black,
    is_active: false,
    is_visible: true,
  });
};

export const setDataButtonBotton = (
  status = true,
  tg,
  text = "RUUUM", // текст кнопки
  colorText = color.bgc__red, // цвет текста кнопки
  colorBackground = color.bgc__black // бекграунд кнопки
) => {
  const eventHandler = () => {
    console.log("search bug");
    document.querySelector(".btn-click").click();
    return () => tg.MainButton.offClick(eventHandler);
  };

  if (status) {
    // if(!tg.MainButton.isVisible)

    tg.MainButton.onClick(eventHandler);
    tg.MainButton.setParams({
      text_color: colorText,
      text: text,
      color: colorBackground,
      is_active: true,
      is_visible: true,
    });
  } else {
    if (!tg.MainButton?.isActive) return;
    tg.MainButton.setParams({
      text_color: colorText,
      text: text,
      color: colorBackground,
      is_active: false,
      is_visible: false,
    });
  }
};
/**
 *
 * @param {link web telegrma} tg
 * @param {values form} values
 * @param {switch where check} action
 * @param {text name button} textButton
 * @param {callback data from request} callback
 * @returns
 */
export const activeButtonBootomForConfirm = (
  tg,
  values,
  action,
  textButton = "подтвердить",
  callback = () => {},
) => {
  if (!!!tg.initDataUnsafe?.query_id) return;
  const checkValueCondition = (values, tg, action) => {
    switch (action) {
      case "country-text":
        return values.city_id && values.text && !tg.MainButton?.isActive;
      case "text":
        return values.text && !tg.MainButton?.isActive;
      case "username":
        return values.username && !tg.MainButton?.isActive;
      case "searchReview":
        return values.username && !tg.MainButton?.isActive;
      case "review":
        return values.username && values.text && !tg.MainButton?.isActive;
      case "deleteApplication":
        return !!values.length && !tg.MainButton?.isActive;
      case "search":
        return (
          values.generation_id &&
          values.brand_id &&
          values.model_id &&
          !tg.MainButton?.isActive
        );
      case "payment":
        return (
          values.access &&
          values.bank &&
          values.period &&
          !tg.MainButton?.isActive
        );
      case "unit-spare":
        return (
          values.isPhone &&
          values.text &&
          values.generation_id &&
          values.brand_id &&
          values.model_id &&
          !tg.MainButton?.isActive
        );
      default:
        return (
          values.text &&
          values.generation_id &&
          values.brand_id &&
          values.model_id &&
          !tg.MainButton?.isActive
        );
    }
  };

  if (checkValueCondition(values, tg, action)) {
    console.log("click checkValueCondition");
    setDataButtonBotton(
      true,
      tg,
      textButton,
      color.bgc__white,
      tg.themeParams.button_color
    );
    return;
  }
};

export const getOptions = (data) => {
  let options = [];
  if (Array.isArray(data) && data.length) {
    data.map((el) => {
      return options.push({
        title: el.name,
        value: el.id || "",
        image: el?.image || "",
      });
    });
  }
  return options;
};
