import Api from "../../api";

export const helpers = (store) => {
  store.on("@init", () => ({ activeBackMenu: false }));
  store.on("setActiveHeaderText", ({ activeBackMenu }, obj) => ({
    activeBackMenu: obj,
  }));

  store.on("@init", () => ({ userStatus: null }));
  store.on("setUserStatus", ({ userStatus }, obj) => ({ userStatus: obj }));
  store.on(
    "requestUserStatus",
    async ({ userStatus, tg }, obj, { dispatch }) => {
      let chatId = tg.initDataUnsafe;
      const userId = chatId?.user?.id ?? 1797304609
      const user = chatId?.user;
      const first_name = user?.first_name;
      const last_name = user?.last_name;
      const username = user?.username;
      let params = {
        telegram_id: userId,
        first_name: first_name,
        last_name: last_name,
        username: username,
      };
      const res = await Api.get(obj.url, params);
      dispatch("setUserStatus", res.user_status);
    }
  );

  store.on("@init", () => ({ textHeader: "" }));
  store.on("setTextHeader", ({ textHeader }, obj) => ({ textHeader: obj }));

  store.on("@init", () => ({ tg: window.Telegram.WebApp }));

  store.on("@init", () => ({ selectItemMenu: 999 }));
  store.on("setItemMenu", ({ selectItemMenu }, obj, { dispatch }) => ({
    selectItemMenu: obj,
  }));

  store.on("@init", () => ({ messagePopup: null }));
  store.on("setMessagePopup", ({ messagePopup }, obj, { dispatch }) => ({
    messagePopup: obj,
  }));

  store.on("@init", () => ({ schemaColor: "light" }));
  store.on("setSchemaColor", ({}, obj, {}) => ({ schemaColor: obj }));

  store.on("@init", () => ({ checkPrevObject: "{}" }));
  store.on("setCheckPrevObject", ({}, obj, {}) => ({
    checkPrevObject: JSON.stringify(obj),
  }));

  store.on("@init", () => ({
    modal: {
      show: false,
      content: null,
    },
  }));
  store.on("openModal", ({ modal }, obj, { dispatch }) => ({
    modal: { ...obj },
  }));
  store.on("closeModal", ({ modal }, obj, { dispatch }) => ({
    modal: { ...obj, show: false },
  }));
};
