import classNames from "classnames";
import React, { useState } from "react";
import Icon from "../Icon/Icon";
import { spinner } from "../../images";

import style from './styles/cardApplication.module.scss';
import { useStoreon } from "storeon/react";
import ViewsImage from "../ViewsImage";

const CardApplication = ({
    listAplication,
    noAction,
    handlerInCatalogApplication = () => { },
    handlerChangeApplication = () => { },
    handlerDeleteApplication = () => { },
}) => {
    const [activeStateButton, setActiveStateButton] = useState(null);
    const { dispatch } = useStoreon();


    const handlerChangeState = (id) => {
        setActiveStateButton(id)
    }

    const openModalImage = (images) => {

    dispatch('openModal', {
            show: true,
            content: <ViewsImage
                images={images}
            />
           
        })
    }

    return (
        <React.Fragment>
            <div
                className={style['create-in-parsing__menu-main-container-list']}
            >

                <ul
                    className={style['create-in-parsing__application-container-list']}
                >
                    {
                        listAplication.results.map(el => {
                            console.log({ el })
                            return (
                                <li
                                    className={ classNames({
                                        [style['create-in-parsing__application-items']]: true,
                                        [style['create-in-parsing__application-image--comment']]: !!el?.images?.length
                                    })
                                    }
                                    data-count={el?.images?.length}
                                    key={`list-app-${el.id}`}
                                >
                                    {
                                        !noAction ?
                                            <div
                                                className={classNames({
                                                    [style['create-in-parsing__menu-edit']]: true,
                                                    [style['active']]: el.id === activeStateButton
                                                })
                                                }
                                            >
                                                <span
                                                    className={style['create-in-parsing__menu-edit-close']}
                                                    onClick={() => setActiveStateButton(null)}
                                                >x</span>
                                                <ul
                                                    className={style['create-in-parsing__menu-edit-list']}
                                                >
                                                    <li
                                                        className={style['create-in-parsing__menu-edit-list-item']}
                                                        onClick={() => handlerInCatalogApplication(el.id)}
                                                    >
                                                        в каталог
                                                    </li>
                                                    <li
                                                        className={style['create-in-parsing__menu-edit-list-item']}
                                                        onClick={() => handlerChangeApplication(el.id)}
                                                    >
                                                        редактировать
                                                    </li>
                                                    <li
                                                        className={style['create-in-parsing__menu-edit-list-item']}
                                                        onClick={() => handlerDeleteApplication(el.id)}
                                                    >
                                                        удалить
                                                    </li>
                                                </ul>
                                            </div>
                                            : null
                                    }
                                    <div
                                        className={classNames({
                                            [style['create-in-parsing__application-wrap']]: true,
                                            [style['active']]: el.id === activeStateButton && !noAction,
                                        })}
                                        onClick={() => !noAction ? handlerChangeState(el.id) : null}                                        
                                        >

                                        <div
                                            className={style['create-in-parsing__application-list-container']}
                                            
                                            >
                                            <div
                                                className={style['create-in-parsing__application-name-user']}
                                                onClick={() => !noAction ? null : window.open(el.user_data.user_url)}
                                            >
                                                <span
                                                    className={style['create-in-parsing__application-name-brand']}
                                                >
                                                    {el.user_data.user_profile_name}
                                                </span>
                                            </div>
                                            <p>
                                                <span
                                                    className={style['create-in-parsing__application-name-brand']}
                                                >
                                                    {el.brand}
                                                </span>
                                                <span
                                                    className={style['create-in-parsing__application-name-model']}
                                                >
                                                    {el.model}
                                                </span>
                                                <span
                                                    className={style['create-in-parsing__application-name-generation']}
                                                >
                                                    {el.generation}
                                                </span>
                                            </p>
                                            <p
                                                className={style['create-in-parsing__application-description']}
                                            >{el.text}</p>
                                        </div>
                                        <Icon
                                            className={classNames({
                                                [style['create-in-parsing__application-image']]: !!!el?.images[0]
                                            })}
                                            image={el?.images[0]?.url ? el.images[0].url : spinner}
                                            width={60}
                                            height={60}
                                            onClick={() => el?.images?.length ?
                                                openModalImage(el.images)
                                                : null
                                            }
                                        >                                           
                                        </Icon>
                                    </div>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        </React.Fragment>
    )
}

export default CardApplication;