import React, { useState } from 'react';
import Icon from '../Icon/Icon';
import ImageUpload from './ImageUpload/ImageUpload'
import style from './styles/uploadimage.module.scss';
import { bascket } from '../../images';
import { useStoreon } from 'storeon/react';
/**
 * uploadFiles - setFieldValue('uploadFiles', files);
 *  
 * @param {
 *  uploadFiles
 *  label
 *  values
 * 
 * *} param0 
 * @returns 
 */
const FormUploadImage = ({
  multiple,
  values,
  setFieldValue,
  styleIcon,
  label,
  onClick,
  callback = () => { },
  image,
}) => {

  const { tg } = useStoreon('tg')

  const fileInputRef = React.useRef(null);
  const [images, setImages] = useState([]);

  return (
    <div className={style['upload-image__form-upload']}>
      <p className={style['upload-image__form-upload-desc']}>
        {!images.length ? label : 'добавить изображение'}
      </p>
      <ImageUpload
        setFieldValue={(e) => console.log({ e })}//?! необходимо допилить сброс изображения
      >
        {({ preview, onSelectFile, selectedFile, onSelectFiles, isDragActive, getRootProps }) => {
          const deleteImage = (elDel) => {
            setImages(c => [...c.filter((el, i) => i !== elDel)])
            setFieldValue('uploadFiles', [...images.filter((el, i) => i !== elDel)]);
            callback([...images.filter((el, i) => i !== elDel)]);
            onSelectFiles([...images.filter((el, i) => i !== elDel)]);
          }
          if (preview === undefined) preview = []
          if (!Array.isArray(preview)) {
            preview = [preview];
          }

          return (
            <>
              <ul className={style['upload-image__form-upload-list']}>
                {
                  preview.map((el, i) => {
                    return (
                      <li key={i} className={style['upload-image__form-upload-item']}>
                        <img
                          crossOrigin="anonymous"
                          className={style['upload-image__form-upload-image']}
                          src={el}
                        />
                        <div
                          className={style['upload-image__form-image--delete']}
                          onClick={() => deleteImage(i)}
                        >
                          <Icon
                            className={style['upload-image__form-image-icon--delete']}
                            image={bascket}
                            width={15}
                            height={15}
                          />
                        </div>
                      </li>
                    );
                  })
                }
              </ul>
              <div
                // {...getRootProps()} 
                className={style['upload-image__form-addprod-image']}
              >
                <div className={style['upload-image__form-addprod-image-wrap']}>
                  <div
                    className={style['upload-image__form-upload-button']}
                  >
                    {
                      image ?
                        <Icon
                          image={image}
                          width={15}
                          height={15}
                          // className={styleIcon}
                          className={style['upload-image__form-icon']}
                        />
                        : <label
                          className={style['upload-image__form-label']}
                        >📎 {
                            !images.length ?
                              'выбрать'
                              : 'добавить'
                          }</label>
                    }

                    <input
                      multiple={tg.platform !== 'android'}
                      ref={fileInputRef}
                      style={{ opacity: 0 }}
                      id="image"
                      type="file"
                      accept=".png, .jpg, .jpeg"
                      name={'image'}
                      onClick={onClick}
                      onChange={(e) => {
                        let files = e.currentTarget.files;
                        let allFiles = [...images, ...files];
                        if (files.length) {
                          const maxCountFile = allFiles.length;
                          if( maxCountFile > 8 ) {
                            tg.showAlert('Привышен лимит добавления файлов, максимальное количество 8');
                            allFiles = allFiles.slice(0,8);
                            files = [...files].slice(0,8);
                          }
                          setImages(allFiles)
                          setFieldValue('uploadFiles', allFiles);
                          callback(allFiles);
                          onSelectFiles(allFiles);
                        }
                      }}
                    />
                  </div>
                </div>
              </div>
            </>
          );
        }}
      </ImageUpload>
    </div>
  );
};

export default React.memo(FormUploadImage);
