import classNames from "classnames";
import React from "react";
import { Link } from "react-router-dom";
import Icon from "../Icon/Icon";
import Block from "./DetaliBlock";

import style from './styles/listMainButtonMain.module.scss';
import { useStoreon } from "storeon/react";

const ListMainButtonMain = ({
    navigate,
    dispatch,
    localPath,
    list,
}) => {
    const { tg } = useStoreon('tg');
    const openURl = (url, text) => {
        const callback = (status = false, link) => {
            if(!!status){
                return window.open(link);
            }
            return false;
        }
        tg && +tg.version >= 6?
                tg.showConfirm(`Перейти в группу?`, e => callback(e, url)) 
                : console.log('проблемы с телеграмм')
    }

    return (
        <React.Fragment>
            <Block.BlockContainerList>
                {

                    list.map( el => {
                        return (
                            
                            <Block.ItemList
                                key={el.id}
                                el = { el }
                            >
                                {
                                    el.can_click?.status?
                                        <div 
                                        onClick={() => {                                                              
                                            if(el.id === 30 || el.id === 6){
                                                return openURl(el.url, el.text)                                                
                                            }else{
                                                dispatch('setItemMenu', el.id)
                                                dispatch('context',{})
                                                el.slug ? dispatch('setActiveHeaderText', true) : dispatch('setActiveHeaderText', false)}
                                                return navigate(`${localPath}${el.slug}`,{
                                                    state:{
                                                        action_page_id: el.action_page_id,
                                                        url: window.location.href.split('/').pop(),
                                                        type: el.additional_info,
                                                        name_button: el.text, 
                                                        url_redirect: el.url
                                                    }
                                                })
                                                }
                                            }
                                        >
                                            <Icon
                                                image={el.image}
                                                width={50}
                                                height={60}
                                                className={style['menu__items-image']}
                                            />
                                            
                                            <Block.ItemName 
                                                name={el.text}
                                            />                                   

                                        </div>
                                        : <div                                             
                                            onClick={() => {                                                
                                                dispatch('setMessagePopup', el.can_click?.message? el.can_click?.message : '');
                                                dispatch('setItemMenu', 31  )
                                                if(el?.can_click?.redirect_payment){
                                                    return navigate('/button_my_profile', {state: {
                                                        name_button: 'мой профиль'
                                                    }});
                                                }
                                                return true;
                                                }
                                            }                                            
                                        >
                                            <span
                                                className={classNames({
                                                    [style['menu__items-attent']]: true,
                                                    [style['menu__items-attent--show']]: el?.can_click?.redirect_payment,
                                                })}
                                            >
                                                ❗
                                            </span>
                                            <Icon
                                                image={el.image}
                                                width={50}
                                                height={45}
                                                className={style['menu__items-image']}

                                            />
                                            
                                            <Block.ItemName 
                                                name={el.text}
                                            />                                   

                                        </div>

                                }
                            </Block.ItemList>
                        )
                    })
                }
            </Block.BlockContainerList>
        </React.Fragment>
    )
}

export default ListMainButtonMain;