import React from "react";
import CheckBox from "../CheckBox/CheckBox";
import Icon from "../Icon/Icon";
import Offset from "../Offset";

import style from './styles/resultsearch.module.scss';

const ResaultSearch = ({
    resaultSearch,
    handlerChangeData,
    selectProduct = [],
}) => {

    return (
        <ul
            className={style['card-filter__container']}
        >
            {
                resaultSearch.brands.length ?
                    resaultSearch.brands.map((el, i) => {
                        return (
                            <li
                                key={`CardSearchFilters-${i}`}
                                className={style['card-filter__item']}
                            >
                                <CheckBox
                                    checked={selectProduct.includes(el.value === '' ? 0 : el.value)}
                                    onChange={res => handlerChangeData(res, 'brand_id')}
                                    name={'brands'}
                                    value={el.value}
                                    id={`check-${el.value}`}
                                />
                                <Icon
                                    image={el.image}
                                    className={style['card-filter__icon-list']}
                                />
                                <span
                                    className={style['card-filter__name']}
                                >
                                    {el.title}
                                </span>
                            </li>
                        )
                    })
                    : null
            }
            {
                resaultSearch.models.length ?
                    <div>
                        <span
                            className={style['card-filter__item-label']}
                        >
                            <strong>Модели: </strong>
                        </span>
                        {
                            resaultSearch.models.map((el, i) => {
                                return (
                                    <li
                                        key={`CardSearchFilters-${i}`}
                                        className={style['card-filter__item']}
                                    >
                                        <Offset mr={10}>
                                            <CheckBox
                                                checked={selectProduct.includes(el.value === '' ? 0 : el.value)}
                                                onChange={res => handlerChangeData(res, 'model_id')}
                                                name={'brands'}
                                                value={el.value}
                                                id={`check-${el.value}`}
                                            />
                                        </Offset>
                                        {
                                            el.image ?
                                                <Icon
                                                    image={el.image}
                                                    className={style['card-filter__icon-list']}
                                                />
                                                : null
                                        }
                                        <span
                                            className={style['card-filter__name']}
                                        >
                                            <p>
                                                <strong>
                                                    {el.brand}
                                                </strong>
                                            </p>
                                            {'Модель: '} {el.title}
                                        </span>
                                    </li>
                                )
                            })
                        }
                    </div>
                    : null

                // 
                // generations
            }
            {
                resaultSearch.generations.length ?
                    <div
                    >
                        <span
                            className={style['card-filter__item-label']}
                        >
                            <strong>Поколения: </strong>
                        </span>
                        {
                            resaultSearch.generations.map((el, i) => {
                                return (
                                    <li
                                        key={`CardSearchFilters-${i}`}
                                        className={style['card-filter__item']}
                                    >
                                        <Offset mr={10}>
                                            <CheckBox
                                                checked={selectProduct.includes(el.value === '' ? 0 : el.value)}
                                                onChange={res => handlerChangeData(res, 'generation_id')}
                                                name={'brands'}
                                                value={el.value}
                                                id={`check-${el.value}`}
                                            />
                                        </Offset>
                                        {
                                            el.image ?
                                                <Icon
                                                    image={el.image}
                                                    className={style['card-filter__icon-list']}
                                                />
                                                : null
                                        }
                                        <span
                                            className={style['card-filter__name']}
                                        >
                                            <p>
                                                <strong>{el.brand} </strong>
                                            </p>
                                            <div>{'Модель:'} {el.model} </div>
                                            <div>{'Поколение:'} {el.title}</div>
                                        </span>
                                    </li>
                                )
                            })
                        }
                    </div>
                    : null

                // 
                // generations
            }
        </ul>
    )
}

export default ResaultSearch;