import { Form, Formik } from "formik";
import * as Yup from "yup";
import React, { useEffect, useState } from "react";
import Phone from "react-phone-number-input";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import {
  activeButtonBootomForConfirm,
  getOptions,
  setDataButtonBottonTrigger,
} from "../../../../helpers";
import Button from "../../../../Views/Button/Button";
import Message from "../../../../Views/Messages/Message/Message";
import Offset from "../../../../Views/Offset";
import Select from "../../../../Views/Select";
import SelectDesktop from "../../../../Views/Select/SelectDesktop";
import Spinner from "../../../../Views/Spinner";
import TextArea from "../../../../Views/TextArea/TextArea";
import FormUploadImage from "../../../../Views/UploadImage/FormUploadImage";
import ErrorMessage from "../../../../Views/ErrorMessage/ErrorMessage";
import PhoneContainer from "../../../../Views/PhoneContainer/PhoneContainer";
import { statusAgreeShowPhone } from "../../../../helpers/const";

const AggregateRequestUnit = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const { dispatch, getContext, tg, userStatus, schemaColor } = useStoreon(
    "getContext",
    "userStatus",
    "tg",
    "schemaColor"
  );
  const [dataRequstBrand, setDataRequestBrand] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });
  const phoneRegExp =
    /^([+]?[\s0-9]+)?(\d{3}|[(]?[0-9]+[)])?([-]?[\s]?[0-9])+$/;

  useEffect(() => {
    dispatch("setTextHeader", state?.name_button);
    dispatch("setActiveHeaderText", true);
    tg.BackButton.show();
    tg.BackButton.onClick(() =>
      navigate("/button_make_request", {
        state: {
          name_button: state?.old_name_button ?? "",
        },
      })
    );
    const params = {
      url: "/api_get_cars/",
      page_id: state?.action_page_id || 2,
      type: "brand",
      dataRequst: (res) => {
        if (!!res?.error)
          return setMessageInForm({
            textMessage:
              res?.error ?? "УПС..., возникла не предвиденная ошибка",
            colorMessage: "#ff0000",
          });
        if (res?.result.length) {
          const copyData = res?.result?.slice();
          setDataRequestBrand((c) => [...c, ...getOptions(copyData)]);
        }
      },
    };
    dispatch("setGetContext", params);

    if (!!!userStatus) {
      dispatch("requestUserStatus", {
        url: "/api_get_page/",
        page_id: state?.action_page_id ?? 2,
      });
    }
  }, [dispatch, state?.action_page_id, navigate, tg.BackButton]);

  const handlerSubmitChooseAggrigate = (data, setFieldValue = () => {}) => {
    console.log("click true");
    const params = {
      ...data,
      url: "/api_create_request/",
      setFieldValue: setFieldValue,
      dataRequst: () => {},
      redirectTo: (res) =>
        navigate("/button_make_request", {
          state: {
            ...state,
            textMessage: res?.textMessage,
            colorMessage: res?.colorMessage ?? "#555050",
          },
        }),
    };
    dispatch("setDataCreateRequest", params);
    return false;
  };

  const handlerChangeData = (changeData) => {
    let params = {
      url: "/api_get_cars/",
      type: changeData.type,
      dataRequst: changeData.handlerChangeDataRequest,
    };
    if (changeData.brand_id) {
      params = {
        ...params,
        brand_id: changeData.brand_id,
      };
    }
    if (changeData.model_id) {
      params = {
        ...params,
        model_id: changeData.model_id,
      };
    }

    dispatch("setGetContext", params);
  };

  const handlerVirtualClick = (values, setFieldValue) => {
    console.log("test click AggregateRequestUnit");
    return () => handlerSubmitChooseAggrigate(values, setFieldValue);
  };

  console.log({ schemaColor });

  return (
    <React.Fragment>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      {Array.isArray(getContext) ? (
        <Formik
          validationSchema={() => {
            return Yup.object().shape({
              phone_number: Yup.string().matches(
                phoneRegExp,
                "Не верный формат номера"
              ),
            });
          }}
          initialValues={{
            optionsBrand: dataRequstBrand,
            optionsModel: [],
            optionsGeneration: [],
            type: state?.type ?? "unit",
            brand_id: "",
            model_id: "",
            generation_id: "",
            text: "",
            uploadFiles: null,
            phone_number: "",
            isPhone: !(+userStatus === statusAgreeShowPhone),
          }}
          onSubmit={handlerSubmitChooseAggrigate}
        >
          {({ values, errors, setFieldValue }) => {
            const handlerChangeDataRequest = (data) => {
              let options = getOptions(data.results);
              if (data.type === "model") setFieldValue("optionsModel", options);
              if (data.type === "generation")
                setFieldValue("optionsGeneration", options);
            };
            return (
              <React.Fragment>
                <Form
                  style={{
                    width: "100%",
                    position: "relative",
                  }}
                >
                  <Offset mt={20} mb={40}>
                    {tg.platform === "tdesktop" ? (
                      <SelectDesktop
                        options={values.optionsBrand}
                        value={values.brand_id}
                        placeholder={"сделайте Ваш выбор"}
                        addClass={"select__dropdown-list-car"}
                        name={"brand"}
                        id={1}
                        style={{ zIndex: 998 }}
                        onClick={(e) => {
                          const value = e.target.getAttribute("value");
                          setFieldValue("brand_id", value);
                          setFieldValue("generation_id", "");
                          setFieldValue("model_id", "");
                          setDataButtonBottonTrigger(false, tg);
                          handlerChangeData({
                            type: "model",
                            brand_id: value,
                            handlerChangeDataRequest: (res) => {
                              const copyData = res.result.slice();
                              handlerChangeDataRequest({
                                results: copyData,
                                type: res.type,
                              });
                            },
                          });
                        }}
                      />
                    ) : (
                      <Select
                        options={values.optionsBrand}
                        value={values.brand_id}
                        placeholder={"сделайте Ваш выбор"}
                        addClass={"select__dropdown-list-car"}
                        name={"brand"}
                        id={1}
                        style={{ zIndex: 998 }}
                        onClick={(e) => {
                          const value = e.target.value;
                          setFieldValue("brand_id", value);
                          setFieldValue("generation_id", "");
                          setFieldValue("model_id", "");
                          setDataButtonBottonTrigger(false, tg);
                          handlerChangeData({
                            type: "model",
                            brand_id: value,
                            handlerChangeDataRequest: (res) => {
                              const copyData = res.result.slice();
                              handlerChangeDataRequest({
                                results: copyData,
                                type: res.type,
                              });
                            },
                          });
                        }}
                      />
                    )}
                    {tg.platform === "tdesktop" ? (
                      <SelectDesktop
                        options={values.optionsModel}
                        disable={
                          !values.brand_id && !values.optionsModel.length
                        }
                        value={values.model_id}
                        placeholder={"сделайте Ваш выбор"}
                        addClass={"select__dropdown-list-car"}
                        name={"model"}
                        id={2}
                        style={{ zIndex: 997 }}
                        onClick={(e) => {
                          const value = e.target.getAttribute("value");
                          setFieldValue("model_id", value);
                          setFieldValue("generation_id", "");
                          setDataButtonBottonTrigger(false, tg);
                          handlerChangeData({
                            type: "generation",
                            model_id: value,
                            handlerChangeDataRequest: (res) => {
                              const copyData = res.result.slice();
                              handlerChangeDataRequest({
                                results: copyData,
                                type: res.type,
                              });
                            },
                          });
                        }}
                      />
                    ) : (
                      <Select
                        options={values.optionsModel}
                        disable={
                          !values.brand_id && !values.optionsModel.length
                        }
                        value={values.model_id}
                        placeholder={"сделайте Ваш выбор"}
                        addClass={"select__dropdown-list-car"}
                        name={"model"}
                        id={2}
                        style={{ zIndex: 997 }}
                        onClick={(e) => {
                          const value = e.target.value;
                          setFieldValue("model_id", value);
                          setFieldValue("generation_id", "");
                          setDataButtonBottonTrigger(false, tg);
                          handlerChangeData({
                            type: "generation",
                            model_id: value,
                            handlerChangeDataRequest: (res) => {
                              const copyData = res.result.slice();
                              handlerChangeDataRequest({
                                results: copyData,
                                type: res.type,
                              });
                            },
                          });
                        }}
                      />
                    )}
                    {tg.platform === "tdesktop" ? (
                      <SelectDesktop
                        options={values.optionsGeneration}
                        value={values.generation_id}
                        disable={
                          !values.model_id && !values.optionsGeneration.length
                        }
                        placeholder={"сделайте Ваш выбор"}
                        addClass={"select__dropdown-list-car"}
                        name={"generation"}
                        id={3}
                        style={{ zIndex: 996 }}
                        onClick={(e) => {
                          const value = e.target.getAttribute("value");
                          setFieldValue("generation_id", value);
                          if (!value){
                            return setDataButtonBottonTrigger(false, tg);
                          }
                          activeButtonBootomForConfirm(
                            tg,
                            {
                              ...values,
                              generation_id: value,
                              isPhone: (+userStatus === statusAgreeShowPhone)? values.phone_number ? true : false : true
                            },
                            "unit-spare",
                            "Опубликовать"
                          );
                        }}
                      />
                    ) : (
                      <Select
                        options={values.optionsGeneration}
                        value={values.generation_id}
                        disable={
                          !values.model_id && !values.optionsGeneration.length
                        }
                        placeholder={"сделайте Ваш выбор"}
                        addClass={"select__dropdown-list-car"}
                        name={"generation"}
                        id={3}
                        style={{ zIndex: 996 }}
                        onClick={(e) => {
                          const value = e.target.value;
                          setFieldValue("generation_id", value);
                          if (!value){
                            return setDataButtonBottonTrigger(false, tg);
                          }
                          activeButtonBootomForConfirm(
                            tg,
                            {
                              ...values,
                              generation_id: value,
                              isPhone: (+userStatus === statusAgreeShowPhone)? values.phone_number ? true : false : true
                            },
                            "unit-spare",
                            "Опубликовать"
                          );
                        }}
                      />
                    )}
                    <Offset mt={20} />
                    <TextArea
                      className={"textarea-application"}
                      value={values.text}
                      name={"text"}
                      placeholder={"Введите данные для запроса"}
                      height={110}
                      onChange={(e) => {
                        const value = e.target.value;
                        setFieldValue("text", value);
                        if (!value){
                          return setDataButtonBottonTrigger(false, tg);
                        }
                        activeButtonBootomForConfirm(
                          tg,
                          {
                            ...values,
                            text: value,
                            isPhone: (+userStatus === statusAgreeShowPhone)? values.phone_number ? true : false : true
                          },
                          "unit-spare",
                          "Опубликовать"
                        );
                      }}
                    />
                    {+userStatus === statusAgreeShowPhone ? (
                      <Offset mt={20}>
                        <PhoneContainer>
                          <Phone
                            international
                            initialValueFormat="national"
                            placeholder={"Укажите номер телефона"}
                            value={values.phone_number}
                            name={"phone_number"}
                            defaultCountry={"RU"}
                            smartCaret={true}
                            limitMaxLength={true}
                            focusInputOnCountrySelection={true}
                            className={"form-input-number-phone"}
                            onChange={(phone) => {
                              if (!phone || phone.length < 8) {
                                setFieldValue("phone_number", phone);
                                setFieldValue("isPhone", false);
                                return setDataButtonBottonTrigger(false, tg);
                              }
                              setFieldValue("phone_number", phone);
                              setFieldValue("isPhone", true);
                              activeButtonBootomForConfirm(
                                tg,
                                {
                                  ...values,
                                  phone_number: phone,
                                  isPhone: true,
                                },
                                "unit-spare",
                                "Опубликовать"
                              );
                            }}
                          />
                          {!values?.phone_number && (
                            <ErrorMessage>
                              {" "}
                              Поле не может быть пустым{" "}
                            </ErrorMessage>
                          )}
                        </PhoneContainer>
                      </Offset>
                    ) : null}
                    <FormUploadImage
                      multiple
                      values={values}
                      setFieldValue={setFieldValue}
                      label={"прикрепить изображения"}
                    />

                    <button
                      className="btn-click"
                      onClick={() => handlerVirtualClick(values, setFieldValue)}
                    ></button>
                    {!!!tg.initDataUnsafe?.query_id ? (
                      <Button
                        disabled={
                          !(
                            values.text &&
                            values.generation_id &&
                            values.brand_id &&
                            values.model_id &&
                            (+userStatus === statusAgreeShowPhone
                              ? !!values?.phone_number
                              : true)
                          )
                        }
                        type={"submit"}
                        addClass={"form-submit__btn"}
                        active={
                          values.text &&
                          values.generation_id &&
                          values.brand_id &&
                          values.model_id &&
                          (+userStatus === statusAgreeShowPhone
                            ? !!values?.phone_number
                            : true)
                        }
                      >
                        <span>
                          <span>создать</span>
                        </span>
                      </Button>
                    ) : null}
                  </Offset>
                </Form>
              </React.Fragment>
            );
          }}
        </Formik>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};
export default AggregateRequestUnit;
