import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Spinner from '../../../../../Views/Spinner';

import style from './styles/ourWarrantlyAdmin.module.scss';

const OurWarrantlyAdmin = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg } = useStoreon('tg');
    const [newText, setNewText] = useState('');

    useEffect(() => {
        dispatch('setTextHeader', state?.name_button);
        dispatch('setActiveHeaderText', true);
        tg.BackButton.show();
        tg.BackButton.onClick(() => {
            dispatch('context', {})
            return navigate('/button_rating/button_garant_rating', {
                state: {
                    name_button: state?.old_name_button ?? '',
                }
            })
        }
        );

        const paramsList = {
            url: '/api_get_page/',
            page_id: 7,
            dataRequst: (resAdmin) => {
                setNewText(resAdmin.result?.message?.text.split(' ').map((el, i) => el.includes('https://') ? `<a href=${el}>${el}</a>` : el).join(' '))
            },
        };

        dispatch('setGetContext', paramsList);
        const timer = setTimeout(() => {
            dispatch('context', {})
            return clearTimeout(timer)
        }, 1000)
    }, [dispatch, state?.action_page_id, tg.BackButton, navigate]);

    return (

        <React.Fragment>
            {
                !!newText.length ?
                    <React.Fragment>
                        <div
                            className={style['detali-info__container']}
                            dangerouslySetInnerHTML={{ __html: newText }}>
                        </div>
                    </React.Fragment>
                    : <Spinner />
            }


        </React.Fragment>
    )
}
export default OurWarrantlyAdmin;