import classNames from "classnames";
import React from "react";
import { Link, useNavigate } from "react-router-dom";
import Icon from "../Icon/Icon";
import Block from "./DetaliBlock";

import style from './styles/listmainbutton.module.scss';
import { useStoreon } from "storeon/react";

const ListMainButton = ({
    old_name_button,
    localPath,
    dispatch,
    list,
}) => {
    const navigate = useNavigate();
    const { tg } = useStoreon('tg');
    const openURl = (url, text) => {
        const callback = (status, link) => {
            if(status){
                return window.open(link);
            }
            return false;
        }
        tg.showConfirm(`Перейти в группу?`, e => callback(e, url)) 
    } 
    return (
        <React.Fragment>
            <Block.BlockContainerList>
                {

                    list.map( ( el, i ) => {
                        return (                            
                            <Block.ItemList
                                key = { i }
                                el = {el}
                            >                                 
                                {
                                    el.can_click?.status?
                                        <Link 
                                            to={ el.id === 30? '/button_sales' :`${localPath}${el?.send_type? el.send_type : el.slug}`}
                                            state={{
                                                action_page_id: el.action_page_id,
                                                url: window.location.href.split('/').pop(),
                                                type: el.additional_info,
                                                name_button: el?.value? el.value : el.text, 
                                                url_redirect: el.url,
                                                old_name_button: old_name_button,
                                                dataFilters: el?.children,
                                                type: el?.send_type
                                            }}
                                            onClick={() => el.id === 30?
                                                openURl(el.url, el?.value? el.value : el.text)
                                                : el.slug ? dispatch('setActiveHeaderText', true) : dispatch('setActiveHeaderText', false)
                                             }
                                        >
                                            <Icon
                                                image={el.image}
                                                width={50}
                                                height={45}
                                                className={style['menu__items-image']}
                                            />
                                            
                                            <Block.ItemName 
                                                 name={el?.value? el.value : el.text}
                                            />                                   

                                        </Link>
                                        : <div 
                                            style={{
                                                width: '100%',
                                                height: '100%',                                            
                                            }}                                            
                                            onClick={() => {
                                                dispatch('setMessagePopup', el.can_click?.message? el.can_click?.message : '');
                                                dispatch('setItemMenu', 31  )
                                                if(el?.can_click?.redirect_payment){
                                                    return navigate('/button_my_profile', {state: {
                                                        name_button: 'мой профиль'
                                                    }});
                                                }
                                                return true;
                                                }
                                            }                                            
                                        >
                                            <span
                                                className={classNames({
                                                    [style['menu__items-attent']]: true,
                                                    [style['menu__items-attent--show']]: el?.can_click?.redirect_payment,
                                                })}
                                            >
                                                ❗
                                            </span>
                                            <Icon
                                                image={el.image}
                                                width={50}
                                                height={45}
                                                className={style['menu__items-image']}
                                            />
                                            
                                            <Block.ItemName 
                                                name={el?.value? el.value : el.text}
                                            />                                   

                                        </div>

                                }

                            </Block.ItemList>
                        )
                    })
                }
            </Block.BlockContainerList>
        </React.Fragment>
    )
}

export default ListMainButton;