import React from 'react';
import classNames from 'classnames';

import style from './styles/index.module.scss';

const Offset = ({ mb = 0, mt=0, mr=0, ml=0, children,addClass }) => {
  return (
    <div
      className={classNames({
        [style['offset']]: true,
        [addClass]: !!addClass,
        [style[`mb--${mb}`]]: !!mb,
        [style[`mt--${mt}`]]: !!mt,
        [style[`mr--${mr}`]]: !!mr,
        [style[`ml--${ml}`]]: !!ml,
      })}
    >{children}</div>
  );
};

export default React.memo(Offset);
