import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import { getOptionsFilters } from '../../../../helpers';
import CardFilters from '../../../../Views/CardFilters';
import Message from '../../../../Views/Messages/Message/Message';
import Search from '../../../../Views/Search';
import ResaultSearch from '../../../../Views/Search/ResaultSearch';


const FiltersSpareModels = (

) => {
    const typePage = window.location.href.split('/')[window.location.href.split('/').length - 2] //.pop();
    const brandPage = window.location.href.split('/').pop();

    const type_list = 'models';
    const { state } = useLocation();
    const [resaultSearch, setResaultSearch] = useState({
        count: 0,
    });
    const [messageInFormOldMessage, setMessageInFormOldMessage] = useState({
        textMessage: '',
        colorMessage: ''
    });
    const [isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();
    const { dispatch, tg } = useStoreon('getContext', 'tg');
    const [dataRequstBrand, setDataRequestBrand] = useState([])
    const [selectProduct, setSelectProduct] = useState([])
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });

    useEffect(() => {
        dispatch('setTextHeader', state?.name_button);
        dispatch('setActiveHeaderText', true);
        tg.BackButton.show();
        tg.BackButton.onClick(() => navigate(`/button_filters/${typePage}`, {
            state: {
                type: state?.type
            }
        }))

        const paramsDataPage = {
            url: '/api_get_context/',
            page_id: 32,
            dataRequst: res => {
                setMessageInForm({
                    textMessage: res?.result.buttons.filter(el => el.send_type === typePage)[0].children.children.message
                });
                return
            }
        };
        dispatch('setGetContextFilters', paramsDataPage)

        const params = {
            url: '/api_get_filters/',
            type: state?.type ?? typePage,
            list: type_list,
            brand_id: brandPage,

            dataRequst: res => {
                if (!!res?.error) return setMessageInForm({ textMessage: res?.error ?? 'УПС..., возникла не предвиденная ошибка', colorMessage: '#ff0000' })
                const copyData = res?.result?.slice();
                setDataRequestBrand(c => [...getOptionsFilters(copyData)])
                const countEnable = copyData.map(el => el.status ? el.id : null).filter(el => el !== null)
                setSelectProduct(c => [...countEnable]);
            }
        };
        dispatch('setGetContext', params)
    }, [dispatch, navigate, tg.BackButton])

    const handlerChangeData = changeData => {

        console.log({ changeData })
        const item = +changeData.value
        if (selectProduct.includes(item)) {
            setSelectProduct(c => c.filter(el => el !== item));
        } else {
            setSelectProduct(c => [...c, item]);
        }

        let params = {
            url: '/api_set_filters/',
            model_id: item,
            type: state?.type,
            list: type_list,
            dataRequst: res => {
                const countEnable = res.result.map(el => el.status ? el.id : null).filter(el => el !== null)
                setSelectProduct(c => [...countEnable]);
                const copyData = res?.result?.slice();
                setDataRequestBrand(c => [...getOptionsFilters(copyData)])
            },
            redirectTo: () => { },
        };

        dispatch('setDataCreateRequest', params)
    }

    const handlerChangeDataSearch = (changeData, key) => {
        const item = +changeData.value
        if (selectProduct.includes(item)) {
            setSelectProduct(c => c.filter(el => el !== item));
        } else {
            setSelectProduct(c => [...c, item]);
        }

        let params = {
            url: '/api_set_filters/',
            [key]: item,
            type: state?.type ?? typePage,
            dataRequst: res => { },
            redirectTo: () => { },
        };
        dispatch('setDataCreateRequest', params)
    }

    return (
        <React.Fragment>
            <Message
                message={messageInForm.textMessage}
                colorMessage={messageInForm.colorMessage}
            />
            <Search
                setMessageInForm={setMessageInForm}
                setIsLoading={setIsLoading}
                setResaultSearch={setResaultSearch}
                oldMessage={messageInFormOldMessage}
            />

            {
                resaultSearch.count ?
                    <ResaultSearch
                        resaultSearch={resaultSearch}
                        handlerChangeData={handlerChangeDataSearch}
                        selectProduct={selectProduct}
                    />
                    :
                    <CardFilters
                        list={dataRequstBrand}
                        handlerChangeData={handlerChangeData}
                        localPath={`/button_filters/${state?.type ?? typePage}/${brandPage}/`}
                        old_state={state?.dataFilters?.children}
                        selectProduct={selectProduct}
                        name_button={state?.name_button}
                        type_list={type_list}
                        type={state?.type}
                    />
            }
        </React.Fragment>
    )
}
export default FiltersSpareModels;