import FormProfileData from "./FormProfileData";
import CheckBox from "../../CheckBox/CheckBox";

const MessagePayment = ({
    message,
    colorMessage,
    noicon,
    handlerAccessPayment,
    old_access = '',
}) => {


    return (
        <React.Fragment>
            {
                            message?
                                <MessageBlockPayment>
                                    {
                                        !noicon? 
                                            <Icon image={info} width={15} height={15} mr={5} /> 
                                            : null
                                    }

                                    <FormProfileData
                                        message={message}
                                    />
                                   
                                            
                                    {
                                        old_access?
                                            <Button
                                                onClick={() => handlerAccessPayment(old_access)} // old_access
                                               addClass={'payment-access'}
                                            >
                                                Продлить Полный доступ 
                                            </Button>
                                        : null
                                    }
                                </MessageBlockPayment>
                                : null
                                
                            }
        </React.Fragment>
    )
}

export default MessagePayment;